import { motion, AnimatePresence } from 'framer-motion';
import React, { useEffect, useMemo, useRef, useState } from 'react';
import styled from 'styled-components';
import { sellerList } from './seller';
import './styles.css';
const LENGTH = 5;
const SLICE_LENGTH = 5;
export default function App() {
  const start = useRef(0);
  const [sellers, setSellers] = useState(() =>
    sellerList.slice(start.current, start.current + SLICE_LENGTH)
  );
  useEffect(() => {
    const timeId = setInterval(() => {
      setSellers((prev) => {
        start.current = (start.current + LENGTH) % sellerList.length;
        return sellerList.slice(start.current, start.current + SLICE_LENGTH);
      });
    }, 3000);
    return () => {
      clearInterval(timeId);
    };
  }, [setSellers]);
  return (
    <Container>
      <AnimatePresence>
        <Wrapper>
          {sellers.map((front, index) => (
            <Card
              key={front.id}
              layoutId={front.id}
              initial={{ rotateX: -200 }}
              transition={{ duration: 0.35, delay: index * 0.1 }}
              animate={{ rotateX: 0 }}
            >
              <CardThumbnail src={front.typical_image_url} />
              <Name>{front.name}</Name>
            </Card>
          ))}
        </Wrapper>
      </AnimatePresence>
    </Container>
  );
}

const Container = styled.div`
  max-width: 475px;
`;

const Wrapper = styled.div``;

const Card = styled(motion.div)`
  display: flex;
  align-items: center;
  width: 100%;
  height: 80px;
  border: 2px solid black;
  padding: 8px;
  box-sizing: border-box;
  margin: 4px 0;
  background: black;
  color: white;
  position: relative;
  transform-style: preserve-3d;
  &::after {
    content: '';
    width: 100%;
    position: absolute;
    background: #000;
    height: 100%;
    left: 0;
    top: 0;
    transform: rotateX(-180deg);
  }
`;

const CardFront = styled(motion.div)`
  position: absolute;
  display: flex;
  align-items: center;
  background: #000;
  width: 90%;
`;

const CardBack = styled(motion.div)`
  position: absolute;
  display: flex;
  align-items: center;
  background: #000;
  width: 90%;
`;

const CardThumbnail = styled.img`
  width: 45px;
  height: 45px;
  border-radius: 100%;
  border: 2px solid pink;
`;

const Name = styled.span`
  margin: 8px;
  font-weight: 600;
  font-size: 18px;
`;
